body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

h1 {
  font-family: "valorantregular";
  font-size: 70px;
  color: rgb(2, 0, 36);
}

body {
  background: #ee0979; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #ff6a00,
    #ee0979
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.card-list {
  width: 50vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 20px;
}

@font-face {
  font-family: "valorantregular";
  src: url(/react-riot-api/static/media/valorant_font-webfont.a0e43cc0.woff2) format("woff2"),
    url(/react-riot-api/static/media/valorant_font-webfont.d9dafb7b.woff) format("woff");
  font-weight: normal;
  font-style: normal;
}

.card-container {
  display: flex;
  flex-direction: column;
  background-color: #b3350f3b;
  border: 1px solid rgba(126, 126, 126, 0);
  border-radius: 5px;
  padding: 25px;
  cursor: pointer;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform 0.25s ease-out;
  font-family: "valorantregular";
}

.card-container:hover {
  transform: scale(1.05);
}

.search {
  -webkit-appearance: none;
  border: none;
  outline: none;
  padding: 10px;
  width: 160px;
  line-height: 30px;
  margin-bottom: 30px;
  background: transparent;
}

.search::-webkit-input-placeholder {
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

.search:-ms-input-placeholder {
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

.search::-ms-input-placeholder {
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

.search::placeholder {
  color: rgb(0, 0, 0);
  font-weight: bolder;
}

